.show-right {
  animation: 0.5s forwards show-right;
}

@keyframes show-right {
  from {
    opacity: 0;
    transform: translate3d(-30px, 0, 0);
  }
  to {
    opacity: 1;
    transform: translate3d(0, 0, 0);
  }
}
.experiencia__lugares ul li.active {
    background-color: #151515;
    border-left: 2px solid #7fd662;
    font-weight: 600;
  }

